<template>
  <v-container>
    <base-material-card
      class="px-5 py-3 "
      color="indigo"
      icon="mdi-vuetify"
      inline
    >
      <template v-slot:after-heading>
        <div class="display-2 font-weight-light">
          {{ $t('销量实时统计') }}
        </div>
      </template>
      <v-divider class="mt-3"/>
      <v-data-table
        :headers="headers"
        :items="resData"
        group-by="ZEIT"
        :items-per-page=100
        group-desc
      ></v-data-table>
    </base-material-card>
  </v-container>
</template>

<script>
  import { getIndexData } from '../../components/Utlis/api'

  export default {
    data: function () {
      return ({
        dauer: '',
        headers: [
          {
            text: this.$i18n.t('name'),
            align: 'start',
            value: 'name',
            groupable: false,
          },
          { text: this.$i18n.t('count'), value: 'count', align: 'center', groupable: false },
          { text: this.$i18n.t('sumPrice'), value: 'sumPrice', align: 'center', groupable: false },
          { text: this.$i18n.t('timeDuration'), value: 'ZEIT', align: 'center' },
        ],
        resData: [],
      })
    },
    mounted () {
      this.reloadData()
    },
    methods: {
      reloadData () {
        getIndexData().then(res => {
          this.resData = res.everyDurationDishesIncome
          this.resData.forEach(item => {
            item.ZEIT = item.timeDuration + this.$i18n.t('hour')
          })
        })
      },
    },
  }
</script>
